-wrap,\n    form .country_drop_wrap,\n    form #d_sharing,\n    form .ajax-loading {\n      display: none !important; } }\n\n#action_info {\n  background: red;\n }  // display: none !important\n#logo_wrap {\n  display: none !important; }\n\nform {\n  div#form_col1 {\n    display: flex; }\n\n  div#form_col2 {\n    input[type='submit'] {\n      cursor: pointer;\n      border: 0;\n      background-color: $violet;\n      border: 4px;\n      color: $light-violet;\n\n      font-family: $serif;\n      font-size: 18px;\n      padding: 5px 10px;\n      border-radius: 4px;\n      box-shadow: 0px 1px 1px rgba(255,255,255,0.2);\n      margin-top: 10px;\n\n      transition: background-color 0.4s, color 0.4s, font-weight 0.4s;\n\n      &:hover {\n        background-color: $light-violet;\n        color: $violet;\n        font-weight: 800; } }\n\n\n\n    //         .core_field\n\n    //             &:nth-child(2)\n    //                 flex-grow: 1;\n    //                 flex-basis: 0;\n\n\n    //             label\n    //                 font-size: 12px;\n    //                 text-transform: uppercase;\n    //                 display: block;\n    //                 letter-spacing: 0.7pt;\n    //                 color: #333333;\n\n    //             input[type='text'],input[type='email']\n    //                 font-size: 16px;\n    //                 padding: 5px;\n    //                 border-radius: 2px;\n    //                 font-family: $sans-serif;\n    //                 border: 0;\n    //                 background-color: $lightgray;\n    //                 width: calc(100% - 20px) !important;\n    //                 margin-top: 5px;\n\n    //                 &::placeholder\n    //                     color: $lightgray\n\n\n    //                 // &.error_input\n    //                 //     background-color: $error-red;\n\n\n\n\n\n\n    form li {\n        list-style: none; } } }\n\n\n.btn {\n  background-color: green; }\n\n// for specificity\n#navbar {\n  min-height: 4.25rem;\n  position: fixed;\n  background: $primary;\n  width: 100%; }\n\n#navbar .navbar-burger.burger {\n  color: $secondary; }\n\n.navbar .navbar-menu {\n  box-shadow:none !important {} }\n\n.navbar .navbar-title {\n  color: $primary-invert;\n  font-size: 40px;\n  font-weight: $font-weight-strong; }\n\n.navbar .navbar-link {\n  color: $white;\n  font-weight: $font-weight-very-strong; }\n\n.content .taglist {\n  list-style: none;\n  margin-bottom: 0;\n  margin-left: 0;\n  margin-right: 1.5rem;\n  margin-top: 1.5rem;\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: left;\n  align-items: center;\n  li {\n    padding: 0 2rem 1rem 0;\n    margin-bottom: 1.5rem;\n    margin-top: 0; } }\n\n// Helper Classes\n.full-width-image-container {\n  width: 100vw;\n  height: 400px;\n  position: relative;\n  left: 50%;\n  right: 50%;\n  margin: 2em -50vw;\n  background-size: cover;\n  background-position: bottom;\n  display: flex;\n  justify-content: center;\n  align-items: center; }\n\n.full-width-image {\n    width: 100vw;\n    height: 400px;\n    background-size: cover;\n    background-position: bottom;\n    display: flex;\n    justify-content: center;\n    align-items: center; }\n\n.btn {\n    display: inline-block;\n    padding: 12px 16px 10px;\n    font-size: 18px;\n    font-size: 1rem;\n    line-height: 1.25;\n    background-color: #fff;\n    border-radius: .25rem;\n    text-decoration: none;\n    font-weight: 700;\n    color: #CC3700;\n    text-align: center;\n    -webkit-box-shadow: inset 0 0 0 2px #CC3700;\n    box-shadow: inset 0 0 0 2px #f40;\n    -webkit-transition: all .15s ease;\n    transition: all .15s ease; }\n\n.margin-top-0 {\n  margin-top: 0 !important; }\n\n.navbar-item .icon {\n  color: $primary; }\n// Override for use of svg's from https://simpleicons.org/\n.icon svg {\n  width: 1.5rem;\n  height: 1.5rem;\n  fill: currentColor; }\n.navbar-brand .navbar-item.logo {\n  padding: 0 1rem; }\nfooter.footer {\n  padding: 3rem 0rem 0rem;\n  background-color: transparent; }\n\n//Menu overrides\n$menu-item-color: $white-ter;\n$menu-item-hover-color: $black !important;\n$menu-item-hover-background-color: $black;\n$menu-item-active-color: $primary-invert;\n$menu-item-active-background-color:\t$primary-invert;\n$menu-list-border-left: 1px solid $primary-invert;\n$menu-label-color: $white-ter;\n\n\n.menu-label {\n  font-size: 1em !important;\n  text-align: left; }\n.menu-list {\n  list-style: none !important;\n  text-align: left; }\n.social {\n  padding: 2em; }\n.social a {\n    padding: .5em .5em .3em .5em;\n    border-radius: 1em;\n    background-color: $white-ter;\n    margin: .5em;\n    width: 1em;\n    height: 1em;\n    vertical-align: middle;\n    display: inline; }\n\n// blog roll\n.blog-list-item.is-featured {\n  background-color: #d6400033; }\n.blog-list-item header {\n  display: flex;\n  margin-bottom: 1em; }\n.blog-list-item .featured-thumbnail {\n  flex-basis: 35%;\n  margin: 0 1.5em 0 0; }\n\n\n@import \"~bulma\";\n\n\n// responsiveness\n@include tablet-only {\n  .blog-list-item .featured-thumbnail {\n    flex-basis: 50%; } }\n\n@include mobile {\n  .blog-list-item header {\n    display: block; }\n  .blog-list-item .featured-thumbnail {\n    text-align: center;\n    max-width: 70%;\n    margin: 0 0 1em; } }\n\n@include touch {\n  .navbar .navbar-link {\n    color: $link-color-on-light; } }\n\n","@charset \"utf-8\";\n/*! bulma.io v0.7.5 | MIT License | github.com/jgthms/bulma */\n@import \"sass/utilities/_all\";\n@import \"sass/base/_all\";\n@import \"sass/elements/_all\";\n@import \"sass/form/_all\";\n@import \"sass/components/_all\";\n@import \"sass/grid/_all\";\n@import \"sass/layout/_all\";\n","@keyframes spinAround {\n  from {\n    transform: rotate(0deg); }\n  to {\n    transform: rotate(359deg); } }\n","@import \"initial-variables\";\n\n@mixin clearfix {\n  &::after {\n    clear: both;\n    content: \" \";\n    display: table; } }\n\n@mixin center($width, $height: 0) {\n  position: absolute;\n  @if